
.sorting-container {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.sorting-dropdown {
  padding: 0.4em 2em;
  border-radius: 1em;
  width: 250px;
  margin: auto;
  border: 2px solid #5fe2b6;
  cursor: pointer;
}

.sorting-dropdown:hover {
  border: 2px solid #bf0ecb;
  box-shadow: 0.2em 0.2em 0.5em -0.3em #bf0ecb;
}

option {
  border: 1px solid #aff5dd;
  cursor: pointer;
}

option:checked 
{ background-color: #aff5dd; }