.size-list {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}

.filter-size__button {
  padding: 0.5em 0.5em;
  border-radius: 0.5em;
  border: 2px solid #5fe2b6;
  cursor: pointer;
}

.filter-size__button-active {
  border: 2px solid #bf0ecb;
  box-shadow: 0.2em 0.2em 0.5em -0.3em #bf0ecb;
  font-weight: 600;
}

.filter-size__button:hover {
  border: 2px solid #d14f9b;
  box-shadow: 0.2em 0.2em 0.5em -0.3em #d14f9b;
}
