.color-list {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80%;
}

.filter-color {
  display: flex;
}

.filter-color h5 {
  align-self: center;
}

.filter-color__button {
  width: 20px;
  height: 20px;
  border-radius: 200%;
  border: 2px solid #5fe2b6;
  cursor: pointer;
  align-self: center;
}

.filter-color__button:hover {
  border: 2px solid #d14f9b;
}

.filter-color__button-active {
  border: 2px solid #bf0ecb;
}

.filter-color__button-black {
  background-color: #000000;
}

.filter-color__button-lightBlue {
  background-color: #9acdf0;
}

.filter-color__button-white {
  background-color: #FFFFFF;
}

.filter-color__button-pink {
  background-color: #F3BDB0;
}

.filter-color__button-red {
  background-color: #ED0909;
}

.filter-color__button-blue {
  background-color: #355A86;
}

.filter-color {
  display: flex;
  justify-content: space-between;
}