.range-year-value, 
.range-count-value {
  display: table;
  padding: 0.5em 0.5em;
  width: 3em;
  height: 0.8em;
  border-radius: 0.4em;
  border: 2px solid #5fe2b6;
  box-shadow: none;
  vertical-align: middle;
  font-weight: 600;
}

.range-year-container,
.range-count-container {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
}

.range-slider {
  width: 60%; 
}

.range-filters h5 {
  margin-top: 23px;
}