.footer-container {
  width: 1200px;
  margin: auto;
  display: flex;
  justify-content: center;
}

.rsschool {
  margin-left: 20px;
  height: 55px;
  width: 110px;
}

.rsschool:hover path {
  fill: #d14f9b;
}

.copyright {
  color: #680b70;
  font-size: 16px;
  font-weight: 600;
}

.copyright a {
  text-decoration: none;
  font-size: 18px;
}

.copyright a:hover {
  text-decoration: underline;
}

@media screen and (max-width: 1279px) {
  
  .footer-container {
      padding: 0 30px;
      width: 100%;
  }

}
