.filters-container {
  display: grid;
  width: 1200px;
  margin: auto;
  grid-template-columns: 1fr 1fr 1fr;
}

.bool-filters-buttons {
  display: flex;
  justify-content: space-around;
}

.filter-group {
  padding: 0 20px;
}

h4, h5 {
  margin: 0.5em 0;
}

.toggle-filters {
  display: none;
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  
  .filters-container {
    padding: 0 30px;
    width: 100%;
    grid-template-columns: 1fr 1fr;
  }

  .sliders-group {
      display: none;
  }

}

@media screen and (max-width: 767px) {
  
  .filters-container {
    padding: 0 30px;
    width: 100%;
    display: flex;
    flex-direction: column;
  }

  .sliders-group {
      display: none;
  }

  .toggle-filters {
    padding: 0 30px;
    width: 100%;
    height: 25px;
    background: #bf0ecb;
    display: table;
    text-align: center;
    color: #FFF;
    font-weight: 600;
    cursor: pointer;
  }

}