.filter-brand__button {
  margin-top: 10px;
  height: 30px;
  cursor: pointer;
  border: 2px solid #FFF;
  margin-right: 1em;
}

.filter-brand__button-active {
  border: 2px solid #bf0ecb;
  box-shadow: 0.2em 0.2em 0.5em -0.3em #bf0ecb;
}

.filter-brand {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.filter-brand__button:hover {
  border: 2px solid #d14f9b;
  box-shadow: 0.2em 0.2em 0.5em -0.3em #d14f9b;
}