.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

* {
  font: Arial, Helvetica, sans-serif;
  box-sizing: border-box;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.notification {
  position: fixed;
  bottom: 20px;
  right: 20px;
  width: 200px;
  height: 60px;
  background: #FFF;
  border: 1px solid #bf0ecb;
  box-shadow: 0.2em 0.2em 0.5em -0.3em #bf0ecb;
  z-index: 10;
  border-radius: 1em;
}

.notification div {
  margin-top: 10px;
}

@keyframes fade-in {
  0% { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes fade-out {
  0% { opacity: 1; }
  100% { opacity: 0; }
}

.transition-fade-out {
  animation-name: fade-out;
  animation-duration: 2.5s;
}

.transition-fade-in {
  animation-name: fade-in;
  animation-duration: 4s;
}

.hidden {
  display: none;
}