.product-card {
  width: 280px;
  height: 485px;
  border: 1px solid #ccc;
  border-radius: 2em;
  overflow: hidden;
  padding: 1em;
  margin-top: 10px;
  cursor: pointer;
}

.product-card:hover {
  border: 1px solid #d14f9b;
  box-shadow: 0.2em 0.2em 0.5em -0.3em #d14f9b;
}

.product-card--active {
  border: 1px solid #bf0ecb;
  box-shadow: 0.2em 0.2em 0.5em -0.3em #bf0ecb;
}

.product-card img {
  width: 250px;
  height: 250px;
}

.product-card__add {
  display: flex;
  justify-content: center;
}

.size__button {
  padding: 0.5em;
  border: 1px solid #ccc;
  margin-right: 5px;
  border-radius: 0.5em;
}

.size__button--active {
  border: 1px solid #bf0ecb;
}

.size__button:hover {
  border: 1px solid #5fe2b6;
}

.action-buttons {
 display: flex;
 justify-content: space-evenly;
 height: 40px;
}

.add-button {
  background: #9bf1d5;
  border: 2px solid #5fe2b6;
  box-shadow: 0.2em 0.2em 0.5em -0.3em #5fe2b6;
  color: #680b70;
  border-radius: 0.5em;
  margin-top: 0.5em;
  font-weight: 600;
  cursor: pointer;
  padding: 0.5em;
}

.add-button:hover,
.remove-button:hover {
  background: #bcf8e4;
  
}

.remove-button {
  background: #9bf1d5;
  border: 2px solid #5fe2b6;
  box-shadow: 0.2em 0.2em 0.5em -0.3em #5fe2b6;
  color: #680b70;
  border-radius: 0.5em;
  margin-top: 0.5em;
  font-weight: 600;
  cursor: pointer;
  padding: 0.3em 0.5em;
  display: table;
}

.add-button:active,
.remove-button:active {
  border-top: 1px solid #5fe2b6;
  border-bottom: 1px solid #5fe2b6;
}

.disabled,
.disabled:hover {
  cursor: not-allowed;
  background: rgb(247, 245, 245);
  border:2px solid #ccc;
}

img.remove-button__img {
  height: 14px;
  width: 14px;
  vertical-align: middle;
  margin-left: 7px;
}

.brandName, 
.product-card__name {
  color: #680b70;
  font-weight: 600;
}

.product-card__price {
  margin: 7px auto;
}

.product-card__price span { 
  color: #680b70;
  font-weight: 600;
}

.product-card__name {
  height: 40px;
}

.product-card__brand {
  margin: 7px auto;
}

.product-card__count {
  font-size: 14px;
  line-height: 2;
}

@media screen and (min-width: 768px) and (max-width: 1279px) {
  
  .product-card img {
    max-width: 250px;
  }

}